const AvailableMeals = () => {
  const mealsList = VegPizza.map((meal) => (
    <li>
      <MealItem
        key={meal.id}
        name={meal.name}
        description={meal.description}
        size={meal.size}
        price={meal.price}
        image={meal.img}
      />
    </li>
  ));
  const mealsList2 = NonVegPizza.map((meal) => (
    <li>
      <MealItem
        key={meal.id}
        name={meal.name}
        description={meal.description}
        size={meal.size}
        price={meal.price}
        image={meal.img}
      />
    </li>
  ));
  const mealsList3 = Pasta.map((meal) => (
    <li>
     
      <MealItem
        key={meal.id}
        name={meal.name}
        description={meal.description}
        size={meal.size}
        price={meal.price}
        image={meal.img}
      />
    </li>
  ));


  return (
    <section className="meals">
      <ul>
        <Card>
          <h1>Veg Pizza</h1>
          <hr></hr> {mealsList}
        </Card>
        <Card>
          <h1>Non Veg Pizza</h1> {mealsList2}
        </Card>
        <Card>
          <h1>Pasta</h1> {mealsList3}
        </Card>
      </ul>
    </section>
  );
};

export default AvailableMeals;